const useScheduleData = (initialSemesterId: string | null = 'fall-2024') => {
  const { user } = useAuth();
  const [semesters, setSemesters] = useState<Semester[]>([]);
  const [loading, setLoading] = useState(true);
  const [selectedSemesterId, setSelectedSemesterId] = useState<string | null>(initialSemesterId);
  const [selectedTeacher, setSelectedTeacher] = useState<string>("");

  useEffect(() => {
    setLoading(true);
    if (!user) return;
    const unsub = onSnapshot(collection(db, 'semesters'), (snapshot) => {
        const allSemesters = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() } as Semester));
        setSemesters(allSemesters);
        if (allSemesters.length > 0 && !selectedSemesterId) {
            const defaultSemester = allSemesters.find(s => s.id === initialSemesterId) || allSemesters[0];
            setSelectedSemesterId(defaultSemester.id);
        }
        setLoading(false);
    }, (error) => {
        console.error("Error fetching semesters: ", error);
        setLoading(false);
    });
    return () => unsub();
  }, [initialSemesterId, selectedSemesterId, user]);

  const selectedSemester = useMemo(() => {
    return semesters.find(s => s.id === selectedSemesterId)
  }, [semesters, selectedSemesterId]);

  const availableTeachers = useMemo(() => {
    if (!selectedSemester) return [];
    return user?.activeRole === 'teacher'
        ? selectedSemester.teachers.filter(t => t === user.name)
        : selectedSemester.teachers;
  }, [selectedSemester, user]);

  useEffect(() => {
    if (availableTeachers.length > 0) {
        if (user?.activeRole === 'teacher' && user.name && availableTeachers.includes(user.name)) {
            setSelectedTeacher(user.name);
        } else if (!selectedTeacher || !availableTeachers.includes(selectedTeacher)) {
            setSelectedTeacher(availableTeachers[0]);
        }
    }
  }, [availableTeachers, user, selectedTeacher]);

  const processScheduleData = useCallback((semester: Semester | undefined, teacher: string): ProcessedSession[] => {
      if (!semester || !teacher || !semester.masterSchedule?.[teacher]) {
          return [];
      }
      const teacherSchedule = semester.masterSchedule[teacher];
      const newProcessedSessions: ProcessedSession[] = [];
      Object.entries(teacherSchedule).forEach(([day, sessions]) => {
          sessions.forEach(session => {
              const timeMatch = session.time.match(/(\d+):\d{2}\s*(AM|PM)?/i);
              if (!timeMatch) return;
              let startHour = parseInt(timeMatch[1]);
              const isPM = timeMatch[2]?.toLowerCase() === 'pm';
              if (isPM && startHour !== 12) startHour += 12;
              if (!isPM && startHour === 12) startHour = 0;
              const startRow = startHour - 9;
              if (startRow >= 0 && startRow < 13) {
                   newProcessedSessions.push({ ...session, day, startRow });
              }
          });
      });
      return newProcessedSessions;
  }, []);

  const processedSessions = useMemo(() => 
    processScheduleData(selectedSemester, selectedTeacher),
  [selectedSemester, selectedTeacher, processScheduleData]);

  return {
    loading,
    semesters,
    selectedSemesterId,
    setSelectedSemesterId,
    selectedSemester,
    selectedTeacher,
    setSelectedTeacher,
    availableTeachers,
    processedSessions
  };
};

const AddStudentDialog = ({ session, semester, teacherName, onStudentAdded }: { session: ProcessedSession, semester: Semester, teacherName: string, onStudentAdded: () => void }) => {
    const [isOpen, setIsOpen] = useState(false);
    const [allStudents, setAllStudents] = useState<StudentProfile[]>([]);
    const [searchTerm, setSearchTerm] = useState("");
    const { toast } = useToast();

    useEffect(() => {
        if (isOpen) {
            const unsub = onSnapshot(collection(db, 'students'), (snapshot) => {
                setAllStudents(snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() } as StudentProfile)));
            });
            return () => unsub();
        }
    }, [isOpen]);

    const handleAddStudent = async (student: StudentProfile) => {
        const studentToAdd: SessionStudent = { id: student.id, name: student.name, attendance: null };
        const sessionPath = `masterSchedule.${teacherName}.${session.day}`;
        const semesterRef = doc(db, 'semesters', semester.id);

        try {
            await runTransaction(db, async (transaction) => {
                const semesterDoc = await transaction.get(semesterRef);
                if (!semesterDoc.exists()) throw new Error("Semester not found!");
                
                const currentData = semesterDoc.data() as Semester;
                const teacherSchedule = currentData.masterSchedule[teacherName];
                const daySessions = teacherSchedule[session.day];
                const sessionIndex = daySessions.findIndex(s => s.id === session.id);
                if (sessionIndex === -1) throw new Error("Session not found!");

                const updatedStudents = [...daySessions[sessionIndex].students, studentToAdd];
                
                const studentDocRef = doc(db, 'students', student.id);
                const enrolledInEntry = { semesterId: semester.id, teacher: teacherName, sessionId: session.id };
                transaction.update(studentDocRef, { enrolledIn: arrayUnion(enrolledInEntry) });

                const updatePath = `${sessionPath}.${sessionIndex}.students`;
                transaction.update(semesterRef, { [updatePath]: updatedStudents });
            });
            toast({ title: "Student Added", description: `${student.name} has been added to the session.` });
            onStudentAdded();
            setIsOpen(false);
        } catch (error: any) {
            toast({ title: "Error", description: `Failed to add student. ${error.message}`, variant: 'destructive' });
        }
    };

    const filteredStudents = useMemo(() => {
        const sessionStudentIds = session.students.map(s => s.id);
        return allStudents.filter(student => 
            !sessionStudentIds.includes(student.id) &&
            student.name.toLowerCase().includes(searchTerm.toLowerCase())
        );
    }, [allStudents, searchTerm, session.students]);

    return (
        <Dialog open={isOpen} onOpenChange={setIsOpen}>
            <DialogTrigger asChild>
                <Button variant="ghost" size="sm" className="w-full h-full text-xs text-muted-foreground font-normal">
                    <UserPlus className="mr-2 h-3 w-3" /> Add Student
                </Button>
            </DialogTrigger>
            <DialogContent>
                <DialogHeader>
                    <DialogTitle>Add Student to Session</DialogTitle>
                    <DialogDescription>Search for an existing student to add to this session.</DialogDescription>
                </DialogHeader>
                <div className="space-y-4">
                    <Input 
                        placeholder="Search student name..."
                        value={searchTerm}
                        onChange={(e) => setSearchTerm(e.target.value)}
                    />
                    <div className="max-h-64 overflow-y-auto space-y-2">
                        {filteredStudents.length > 0 ? filteredStudents.map(student => (
                            <div key={student.id} className="flex items-center justify-between p-2 border rounded-md">
                                <span>{student.name} ({student.level})</span>
                                <Button size="sm" onClick={() => handleAddStudent(student)}>Add</Button>
                            </div>
                        )) : <p className="text-sm text-muted-foreground text-center">No students found.</p>}
                    </div>
                </div>
            </DialogContent>
        </Dialog>
    );
}

const ScheduleGrid = ({ processedSessions, selectedDay, isMobile, semester, teacherName, onUpdate }: { processedSessions: ProcessedSession[]; selectedDay: string; isMobile: boolean; semester: Semester | undefined; teacherName: string; onUpdate: () => void }) => {
  const allDays = ["Saturday", "Sunday", "Monday", "Tuesday", "Wednesday", "Thursday"];
  const days = isMobile && selectedDay !== 'all' ? [allDays.find(d => d.toLowerCase().startsWith(selectedDay)) || ''] : allDays;
  const timeSlots = Array.from({ length: 13 }, (_, i) => `${i + 9}:00`);
  const { user } = useAuth();
  const { toast } = useToast();

  const handleUpdateAttendance = async (studentId: string, sessionId: string, day: string, status: SessionStudent['attendance']) => {
      if (!semester || !user) return;
      const weekStartDate = '2025-06-28'; // This should be dynamic based on selected week
      const attendancePath = `weeklyAttendance.${weekStartDate}.${teacherName}.${sessionId}.${studentId}`;
      try {
          await updateDoc(doc(db, 'semesters', semester.id), {
              [attendancePath]: { status }
          });
          toast({ title: "Attendance updated", description: `Marked as ${status}.`});
          onUpdate();
      } catch (error: any) {
          toast({ title: "Error", description: error.message, variant: 'destructive'});
      }
  };

  const handleRemoveStudent = async (student: SessionStudent, session: ProcessedSession) => {
    if (!semester) return;
    const isTeacher = user?.activeRole === 'teacher';
    const semesterRef = doc(db, 'semesters', semester.id);

    try {
        if (isTeacher) {
            const request = {
                type: 'remove-student',
                status: 'pending',
                date: new Date().toISOString(),
                teacherId: user.id,
                teacherName: user.name,
                details: {
                    studentId: student.id,
                    studentName: student.name,
                    sessionId: session.id,
                    sessionTime: session.time,
                    day: session.day,
                    reason: 'Teacher requested removal from schedule view.',
                    semesterId: semester.id
                }
            };
            await runTransaction(db, async (transaction) => {
                const newRequestRef = doc(collection(db, "requests"));
                transaction.set(newRequestRef, {...request, id: newRequestRef.id });

                const semDoc = await transaction.get(semesterRef);
                const currentData = semDoc.data() as Semester;
                const daySessions = currentData.masterSchedule[teacherName][session.day];
                const sessionIndex = daySessions.findIndex(s => s.id === session.id);
                const studentIndex = daySessions[sessionIndex].students.findIndex(s => s.id === student.id);
                
                const updatePath = `masterSchedule.${teacherName}.${session.day}.${sessionIndex}.students.${studentIndex}.pendingRemoval`;
                transaction.update(semesterRef, { [updatePath]: true });
            });

            toast({ title: "Removal Requested", description: `Request to remove ${student.name} has been sent for approval.` });
        } else { // Admin action
            await runTransaction(db, async (transaction) => {
                const semDoc = await transaction.get(semesterRef);
                const currentData = semDoc.data() as Semester;
                const daySessions = currentData.masterSchedule[teacherName][session.day];
                const sessionIndex = daySessions.findIndex(s => s.id === session.id);
                const studentList = daySessions[sessionIndex].students;
                const updatedStudents = studentList.filter(s => s.id !== student.id);
                
                const studentDocRef = doc(db, 'students', student.id);
                const enrolledInEntry = { semesterId: semester.id, teacher: teacherName, sessionId: session.id };
                transaction.update(studentDocRef, { enrolledIn: arrayRemove(enrolledInEntry) });

                const updatePath = `masterSchedule.${teacherName}.${session.day}.${sessionIndex}.students`;
                transaction.update(semesterRef, { [updatePath]: updatedStudents });
            });
            toast({ title: "Student Removed", description: `${student.name} has been removed from the session.` });
        }
        onUpdate();
    } catch (error: any) {
        toast({ title: "Error", description: `Failed to process removal. ${error.message}`, variant: 'destructive'});
    }
  };


  const formatTimeForDisplay = (time: string) => {
      const date = new Date(`1970-01-01T${time}:00`);
      return date.toLocaleTimeString('en-US', { hour: 'numeric', hour12: true });
  }
  
  if (isMobile && selectedDay === 'all') {
    return <Card className="mt-4"><CardContent className="p-4 text-center text-muted-foreground">Please select a day to view the schedule.</CardContent></Card>;
  }

  if (!processedSessions || processedSessions.length === 0) {
    return <Card className="mt-4"><CardContent className="p-4 text-center text-muted-foreground">No schedule available for the selected teacher.</CardContent></Card>;
  }

  return (
    <div className={cn("grid gap-px bg-border", isMobile ? "grid-cols-[auto_1fr]" : "grid-cols-[auto_repeat(6,_1fr)] -ml-4 -mr-4")}>
      {/* Time Column */}
      <div className="flex flex-col">
        <div className="h-12 bg-background"></div>
        {timeSlots.map(time => (
            <div key={time} className="h-28 flex items-start justify-center bg-background pt-2 px-2 text-xs text-muted-foreground">
                {formatTimeForDisplay(time)}
            </div>
        ))}
      </div>

      {/* Day Columns */}
      {days.map((day) => day && (
        <div key={day} className="relative col-span-1 bg-background">
          <div className="sticky top-16 z-10 bg-background/95 backdrop-blur-sm h-12 flex items-center justify-center font-semibold border-b">
            {day}
          </div>
          <div className="absolute top-12 left-0 w-full h-[calc(13_*_7rem)] grid grid-rows-[repeat(13,_7rem)] gap-px">
             {timeSlots.map((time) => (
                <div key={`${day}-${time}`} className="h-28 border-t"></div>
            ))}
          </div>
          <div className="absolute top-12 left-0 w-full h-full">
            {processedSessions
              .filter(session => session.day === day)
              .map(session => (
                <div
                  key={session.id}
                  className="absolute w-full p-1"
                  style={{ 
                    top: `${session.startRow * 7}rem`, 
                    height: `${session.duration * 7}rem` 
                  }}
                >
                  <Card className="w-full h-full flex flex-col shadow-none">
                    <CardContent className="p-2 flex-grow flex flex-col gap-1">
                      <div className="flex justify-between items-center text-xs">
                        <p className="text-muted-foreground">{session.time}</p>
                        <p className="font-medium">{session.type}</p>
                      </div>
                      <div className="my-1">
                        <Badge variant="secondary" className="font-normal">{session.specialization}</Badge>
                      </div>
                      <Separator className="my-1"/>
                      <div className="flex-grow overflow-y-auto pr-1">
                        {session.students && session.students.length > 0 ? (
                          <ul className="space-y-1 text-xs">
                            {session.students.map((student: SessionStudent) => (
                              <li key={student.id} className={cn("flex justify-between items-center p-1 rounded group/student", student.pendingRemoval && "opacity-50")}>
                                <div className="flex items-center gap-2">
                                  <User className="h-3 w-3 shrink-0" />
                                  <span className="font-medium">{student.name}</span>
                                  {student.pendingRemoval && <Hourglass className="h-3 w-3 text-destructive animate-spin" />}
                                </div>
                                <div className="flex items-center gap-1">
                                    <Popover>
                                        <PopoverTrigger asChild>
                                            <Button variant="ghost" size="icon" className="h-6 w-6 opacity-0 group-hover/student:opacity-100">
                                                <GripVertical className="h-4 w-4" />
                                            </Button>
                                        </PopoverTrigger>
                                        <PopoverContent className="w-auto p-1">
                                            <div className="flex gap-1">
                                                <Button onClick={() => handleUpdateAttendance(student.id, session.id, day, 'present')} variant="ghost" size="icon" className="h-7 w-7"><Check className="text-green-600"/></Button>
                                                <Button onClick={() => handleUpdateAttendance(student.id, session.id, day, 'absent')} variant="ghost" size="icon" className="h-7 w-7"><X className="text-red-600"/></Button>
                                                <Button onClick={() => handleUpdateAttendance(student.id, session.id, day, 'late')} variant="ghost" size="icon" className="h-7 w-7"><Clock className="text-amber-600"/></Button>
                                                <Button onClick={() => handleUpdateAttendance(student.id, session.id, day, 'excused')} variant="ghost" size="icon" className="h-7 w-7"><File className="text-blue-600"/></Button>
                                            </div>
                                        </PopoverContent>
                                    </Popover>
                                    <Button onClick={() => handleRemoveStudent(student, session)} variant="ghost" size="icon" className="h-6 w-6 opacity-0 group-hover/student:opacity-100">
                                        <Trash2 className="h-3 w-3 text-destructive" />
                                    </Button>
                                </div>
                              </li>
                            ))}
                          </ul>
                         ) : (
                            <div className="flex-grow flex items-center justify-center">
                                {semester && user?.activeRole !== 'teacher' ? (
                                    <AddStudentDialog session={session} semester={semester} teacherName={teacherName} onStudentAdded={onUpdate} />
                                ) : (
                                    <p className="text-xs text-muted-foreground">No students enrolled</p>
                                )}
                            </div>
                         )}
                      </div>
                    </CardContent>
                  </Card>
                </div>
              ))}
          </div>
        </div>
      ))}
    </div>
  );
};


export default function DashboardPage() {
    const { user } = useAuth();
    const isMobile = useIsMobile();
    const [_, setForceUpdate] = useState({}); // Helper to force re-renders on data change
    const { 
        loading,
        semesters,
        selectedSemesterId,
        setSelectedSemesterId,
        selectedSemester,
        selectedTeacher,
        setSelectedTeacher,
        availableTeachers,
        processedSessions
    } = useScheduleData();
    const [selectedDay, setSelectedDay] = useState('all');

    const handleUpdate = () => {
        // This is a bit of a hack to force components to re-read data from context/state
        // when underlying firestore data changes.
        setForceUpdate({});
    };

    if (loading) {
        return (
            <div className="flex h-[calc(100vh-8rem)] items-center justify-center">
                <Loader2 className="h-8 w-8 animate-spin text-primary" />
            </div>
        );
    }
    
    return (
        <div className="space-y-4">
            <div className="flex flex-col sm:flex-row items-center justify-between gap-4">
                <h1 className="text-2xl font-headline self-start">Admin Dashboard</h1>
                <div className="flex flex-col sm:flex-row items-stretch sm:items-center gap-2 w-full sm:w-auto">
                    <Button variant="outline" className="w-full sm:w-auto"><UserPlus/> Enroll Student</Button>
                    <Button variant="outline" className="w-full sm:w-auto"><Upload/> Import</Button>
                    <Button variant="outline" className="w-full sm:w-auto"><FileDown/> Export</Button>
                </div>
            </div>
            
            <Card>
                <CardContent className="p-4">
                     <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:flex xl:items-center gap-4">
                        <div className="w-full xl:w-auto flex items-center gap-2">
                            <BarChart3 className="w-5 h-5 text-muted-foreground" />
                            <Select
                                value={selectedSemesterId ?? ""}
                                onValueChange={(id) => {
                                  setSelectedSemesterId(id);
                                  setSelectedTeacher(""); // Reset teacher on semester change
                                }}
                            >
                              <SelectTrigger className="w-full xl:w-[180px]">
                                <SelectValue placeholder="Select a semester" />
                              </SelectTrigger>
                              <SelectContent>
                                {semesters.map(s => <SelectItem key={s.id} value={s.id}>{s.name}</SelectItem>)}
                              </SelectContent>
                            </Select>
                        </div>
                        <div className="w-full xl:w-auto flex items-center gap-2">
                             <Users className="w-5 h-5 text-muted-foreground" />
                             <Select
                                value={selectedTeacher}
                                onValueChange={setSelectedTeacher}
                                disabled={!selectedSemesterId || availableTeachers.length === 0 || user?.activeRole === 'teacher'}
                            >
                              <SelectTrigger className="w-full xl:w-[180px]">
                                <SelectValue placeholder="Select a teacher" />
                              </SelectTrigger>
                              <SelectContent>
                                {availableTeachers.map(t => <SelectItem key={t} value={t}>{t}</SelectItem>)}
                              </SelectContent>
                            </Select>
                        </div>
                        <div className="w-full xl:w-auto flex items-center gap-2">
                             <Calendar className="w-5 h-5 text-muted-foreground" />
                             <ToggleGroup type="single" value={selectedDay} onValueChange={(value) => value && setSelectedDay(value)} variant="outline" size="sm" className="w-full md:w-auto grid grid-cols-4 md:flex">
                                <ToggleGroupItem value="all" aria-label="Toggle all" className="flex-1 md:flex-none">All</ToggleGroupItem>
                                <ToggleGroupItem value="sat" aria-label="Toggle sat" className="flex-1 md:flex-none">Sat</ToggleGroupItem>
                                <ToggleGroupItem value="sun" aria-label="Toggle sun" className="flex-1 md:flex-none">Sun</ToggleGroupItem>
                                <ToggleGroupItem value="mon" aria-label="Toggle mon" className="flex-1 md:flex-none">Mon</ToggleGroupItem>
                                <ToggleGroupItem value="tue" aria-label="Toggle tue" className="flex-1 md:flex-none">Tue</ToggleGroupItem>
                                <ToggleGroupItem value="wed" aria-label="Toggle wed" className="flex-1 md:flex-none">Wed</ToggleGroupItem>
                                <ToggleGroupItem value="thu" aria-label="Toggle thu" className="flex-1 md:flex-none">Thu</ToggleGroupItem>
                             </ToggleGroup>
                        </div>
                        <div className="hidden md:flex items-center gap-2 md:ml-auto">
                            <Calendar className="w-5 h-5 text-muted-foreground" />
                            <span className="text-sm text-muted-foreground">Week of:</span>
                            <div className="flex items-center gap-1">
                                <Button variant="ghost" size="icon" className="h-8 w-8"><ChevronLeft /></Button>
                                <span className="text-sm font-medium">Jun 28-3, 2025</span>
                                <Button variant="ghost" size="icon" className="h-8 w-8"><ChevronRight /></Button>
                            </div>
                        </div>
                    </div>
                </CardContent>
            </Card>

            <div className="overflow-x-auto">
                <ScheduleGrid 
                    processedSessions={processedSessions} 
                    selectedDay={selectedDay} 
                    isMobile={isMobile}
                    semester={selectedSemester}
                    teacherName={selectedTeacher}
                    onUpdate={handleUpdate}
                />
            </div>
        </div>
    );
}

